import React, { useEffect, useRef } from 'react';
import styles from './Section3.module.css';
import tabletKioskImage from '../../assets/images/.webp'; //   import
import phoneImage from '../../assets/images/.webp'; //   import
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const Section3 = () => {
  const highlightRef = useRef(null);
  const sloganContainerRef = useRef(null); //   
  const solutionContainerRef = useRef(null);
  const solutionTextRef = useRef(null); //    
  const solutionImageRef = useRef(null); //    

  useEffect(() => {
    if (!highlightRef.current || !sloganContainerRef.current) {
      console.error('highlightRef  sloganContainerRef  .');
      return;
    }

    // ""   
    gsap.fromTo(
      highlightRef.current,
      { fontSize: '7rem', fontWeight: 700 },
      {
        fontSize: '9rem',
        fontWeight: 800,
        ease: 'none',
        scrollTrigger: {
          trigger: sloganContainerRef.current,
          start: 'top center',
          end: 'bottom center',
          scrub: true,
          markers: false,
        },
      }
    );

    //     
    if (solutionContainerRef.current) {
      gsap.fromTo(
        solutionContainerRef.current,
        { opacity: 0 },
        {
          opacity: 1,
          duration: 1,
          ease: 'power3.out',
          scrollTrigger: {
            trigger: solutionContainerRef.current,
            start: 'top 80%',
            toggleActions: 'restart pause resume pause',
            markers: false,
          },
        }
      );
    }
  }, []);

  return (
    <div className={styles.section3}>
      <div className={styles.sloganContainer} ref={sloganContainerRef}>
        <p>
            
          <br />
          {' '}
          <span className={styles.highlight} ref={highlightRef}>
            
          </span>
            
        </p>
      </div>

      {/*   */}
      <div className={styles.problemContainer}>
        <div className={styles.problemImageContainer}>
          <img src={tabletKioskImage} alt=" " className={styles.problemImage} />
        </div>
        <div className={styles.problemTextContainer}>
          <p>
                    .
                .
                   .
          </p>
        </div>
      </div>

      {/*   */}
      <div className={styles.solutionContainer} ref={solutionContainerRef}>
        <div className={styles.solutionTextContainer} ref={solutionTextRef}>
          <p>
                    .
                      
               <span className={styles.emphasize}>  UI</span> 
               .
          </p>
        </div>
        <div className={styles.solutionImageContainer}>
          <img
            ref={solutionImageRef}
            src={phoneImage}
            alt=" "
            className={styles.solutionImage}
          />
        </div>
      </div>
      <div className={styles.summaryContainer}>
        <p>   </p>
      </div>
    </div>
  );
};

export default Section3;
