import React, { useEffect, useRef, useState } from 'react';
import styles from './Section6Video.module.css';
import KioskVideo from '../../assets/video/ .mp4'; // MP4 
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

// GSAP ScrollTrigger  
gsap.registerPlugin(ScrollTrigger);

const Section6Video = () => {
  const sectionRef = useRef(null); //    
  const headingRef = useRef(null); //    
  const videoRef = useRef(null); //    
  const [isVideoLoaded, setIsVideoLoaded] = useState(false); //    

  useEffect(() => {
    const currentVideo = videoRef.current; //    

    if (!currentVideo) {
      return;
    }

    // IntersectionObserver :  25%    
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setIsVideoLoaded(true); //    
            observer.unobserve(entry.target); //    
          }
        });
      },
      {
        threshold: 0.25, //  25%    
      }
    );

    //    
    observer.observe(currentVideo);

    //  :     
    return () => {
      if (currentVideo) {
        observer.unobserve(currentVideo);
      }
    };
  }, []);

  useEffect(() => {
    if (!headingRef.current) {
      console.error('   .');
      return;
    }

    const gridContainer = document.querySelector(`.${styles.gridContainer}`); //    
    const descriptionContainer = document.querySelector(`.${styles.descriptionContainer}`); //    

    // ScrollTrigger :    overflow  
    ScrollTrigger.create({
      trigger: headingRef.current, //  : 
      start: 'top 80%', //    80%   
      end: 'bottom 20%', //  :  20% 
      onEnter: () => {
        //    
        if (gridContainer) {
          gridContainer.style.overflow = 'visible';
        }
        if (descriptionContainer) {
          descriptionContainer.style.overflow = 'visible';
        }
      },
      onLeave: () => {
        //    
        if (gridContainer) {
          gridContainer.style.overflow = 'hidden';
        }
        if (descriptionContainer) {
          descriptionContainer.style.overflow = 'hidden';
        }
      },
      onEnterBack: () => {
        //     
        if (gridContainer) {
          gridContainer.style.overflow = 'visible';
        }
        if (descriptionContainer) {
          descriptionContainer.style.overflow = 'visible';
        }
      },
      onLeaveBack: () => {
        //    
        if (gridContainer) {
          gridContainer.style.overflow = 'hidden';
        }
        if (descriptionContainer) {
          descriptionContainer.style.overflow = 'hidden';
        }
      },
      markers: false, //   
    });

    // GSAP  :     
    const burnAndShineTimeline = gsap.timeline({ paused: true });

    //   textShadow  
    burnAndShineTimeline
      .to(headingRef.current, {
        color: '#FFFFFF', //    
        duration: 0.1, //   
        ease: 'power1.inOut', //  
      }, 0) //   

      .to(headingRef.current, {
        textShadow: `
          0 0 15px rgba(255, 69, 0, 0.8),
          0 0 30px rgba(255, 140, 0, 0.7),
          0 0 45px rgba(255, 215, 0, 0.5)
        `, //   textShadow 
        duration: 1.5,
        ease: 'power2.out',
      }, 0) //  

      .to(headingRef.current, {
        textShadow: `
          0 0 20px rgba(255, 69, 0, 0.9),
          0 0 40px rgba(255, 140, 0, 0.8),
          0 0 60px rgba(255, 215, 0, 0.6)
        `, //   textShadow 
        duration: 1.5,
        ease: 'power2.in',
      }, '+=1.5') //     1.5  

      .to(headingRef.current, {
        textShadow: `
          0 0 15px rgba(255, 69, 0, 0.8),
          0 0 30px rgba(255, 140, 0, 0.7),
          0 0 45px rgba(255, 215, 0, 0.5)
        `, //   textShadow 
        duration: 1.5,
        ease: 'power2.out',
      }, '+=0') //   

      .yoyo(true) //   
      .repeat(-1); //   

    // ScrollTrigger :      
    ScrollTrigger.create({
      trigger: headingRef.current, //  : 
      start: 'top 80%', //   
      onEnter: () => {
        burnAndShineTimeline.play(); //  
      },
      once: true, //    
      markers: false, //   
    });

    return () => {
      //    ScrollTrigger   
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
      burnAndShineTimeline.kill();
    };
  }, []);

  return (
    <div className={styles.section6VideoWrapper} ref={sectionRef}>
      <div className={styles.gridContainer}>
        {/*   */}
        <div className={styles.videoContainer} ref={videoRef}>
          {isVideoLoaded && (
            <video
              className={styles.video}
              autoPlay
              loop
              muted
              playsInline
              preload="auto"
            >
              <source src={KioskVideo} type="video/mp4" />
              Your browser does not support the video tag.
            </video>
          )}
        </div>
        {/*   */}
        <div className={styles.descriptionContainer}>
          <div className={styles.descriptionheadingBox}>
            <p ref={headingRef} className={styles.heading}>
                 .
            </p>
          </div>
          <div className={styles.descriptionBox}>
            <p>
                   , <br />
                   <span className={styles.highlight}>  </span> . <br />
                     . <br />
                 ,   , <br />
                    UI . <br />
                    .
            </p>
          </div>
          <div className={styles.descriptionBox}>
            <p>
                     ,<br />
                   . <br />
                 ,   , <br />
              <span className={styles.highlight}>    </span> . <br />
                ,     <br />
                UI .
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Section6Video;
