// src/components/Section6/Section6.jsx
import React, { useLayoutEffect, useRef } from 'react';
import styles from './Section6.module.css';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

//  
import kioskMain from '../../assets/images/ .jpg';
import kioskBeverage from '../../assets/images/ .jpg';
import kioskPreviousOrder from '../../assets/images/ .jpg';
import kioskOptionOrder from '../../assets/images/ .jpg';
import kioskOrderComplete from '../../assets/images/ .jpg';
import kioskMenuPhoto from '../../assets/images/ .jpg';
import kioskOptionSelection from '../../assets/images/ .jpg';

gsap.registerPlugin(ScrollTrigger);

const imagesData = [
  {
    src: kioskMain,
    title: 'KIOSK ',
    description: `
          <span class="${styles.highlight}">  UI</span>
       .<br />      
      ,      .
    `,
  },
  {
    src: kioskBeverage,
    title: 'KIOSK ',
    description: `
        , ()    .
      <br />
           ()  <br /><span class="${styles.highlight}">   </span> UI .
      <br />(    )
    `,
  },
  {
    src: kioskMenuPhoto,
    title: 'KIOSK ',
    description: `
      UI   <span class="${styles.highlight}">1 , 2  </span>  <br />
      1     <br />2     .<br />
      <br /> ,    <br />    <br />
      <span class="${styles.highlight}">  </span> . <br />
    `,
  },
  {
    src: kioskPreviousOrder,
    title: 'KIOSK ',
    description: `
           <br />    
      <span class="${styles.highlight}"> </span>    <br />UI .
      <br /><br />
             .
    `,
  },
  {
    src: kioskOptionOrder,
    title: 'KIOSK ',
    description: `
       , ,    <br />{' '}
          . <br />
      <br /> <span class="${styles.highlight}">   </span> <br /> 
          <br />
        .
    `,
  },
  {
    src: kioskOptionSelection,
    title: 'KIOSK ',
    description: `
              <br />
      <span class="${styles.highlight}">  </span>     <br />
        .
    `,
  },
  {
    src: kioskOrderComplete,
    title: 'KIOSK ',
    description: `
       <span class="${styles.highlight}">  </span> <br />
            . <br />
      <br />    <br />
          <br />
      <span class="${styles.highlight}">    </span>  .
    `,
  },
];

const Section6 = () => {
  const sectionRef = useRef(null);
  const wrapperRef = useRef(null);
  const imageRefs = useRef([]);
  const descriptionRefs = useRef([]);

  useLayoutEffect(() => {
    if (!sectionRef.current || !wrapperRef.current) return;

    const images = imageRefs.current;
    const descriptions = descriptionRefs.current;

    //   
    gsap.set(images, { opacity: 0, filter: 'blur(20px)' });
    gsap.set(images[0], { opacity: 1, filter: 'blur(0px)' });
    gsap.set(descriptions, { opacity: 0, y: 50 }); //    
    gsap.set(descriptions[0], { opacity: 1, y: 0 }); //    

    //    
    ScrollTrigger.create({
      trigger: sectionRef.current,
      start: 'top top',
      end: `+=${window.innerHeight * imagesData.length}`,
      pin: wrapperRef.current,
      pinSpacing: true,
      markers: false, //    false 
    });

    //       
    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: sectionRef.current,
        start: 'top top',
        end: `+=${window.innerHeight * imagesData.length}`,
        scrub: true,
        markers: false, //    false 
      },
    });

    imagesData.forEach((data, index) => {
      const currentImage = images[index];
      const nextImage = images[index + 1];
      const currentDescription = descriptions[index];
      const nextDescription = descriptions[index + 1];

      if (nextImage && nextDescription) {
        //      
        tl.to(
          currentImage,
          {
            opacity: 0,
            filter: 'blur(20px)',
            duration: 1.5, //   
            ease: 'power3.inOut',
          },
          `+=0.5` //    
        );

        //      
        tl.fromTo(
          nextImage,
          {
            opacity: 0,
            filter: 'blur(20px)',
          },
          {
            opacity: 1,
            filter: 'blur(0px)',
            duration: 1.5, //   
            ease: 'power3.inOut',
          },
          `-=${0.75}` //    
        );

        //    (  )
        tl.to(
          currentDescription,
          {
            opacity: 0,
            y: -150, //  
            duration: 1.5, //   
            ease: 'power3.inOut',
          },
          `-=${0.75}` //    
        );

        //    (   )
        tl.fromTo(
          nextDescription,
          {
            opacity: 0,
            y: 150, //  
          },
          {
            opacity: 1,
            y: 0, //   
            duration: 1.5, //   
            ease: 'power3.out',
          },
          `-=${0.75}` //    
        );
      }
    });

    return () => {
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill());
    };
  }, []);

  return (
    <div className={styles.kioskSection6} ref={sectionRef}>
      {/*   */}
      <div className={styles.kioskWrapper} ref={wrapperRef}>
        {/*   */}
        <div className={styles.kioskImageContainer}>
          {imagesData.map((data, index) => (
            <img
              key={index}
              src={data.src}
              alt={data.title}
              className={styles.kioskImage}
              ref={(el) => (imageRefs.current[index] = el)}
            />
          ))}
        </div>

        {/*   */}
        <div className={styles.kioskDescriptionContainer}>
          {imagesData.map((data, index) => (
            <div
              key={index}
              className={styles.kioskDescription}
              ref={(el) => (descriptionRefs.current[index] = el)}
            >
              <h2 className={styles.kioskSubtitle}>{data.title}</h2>
              {/* dangerouslySetInnerHTML  HTML   */}
              <p dangerouslySetInnerHTML={{ __html: data.description }}></p>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Section6;
