import React, { useMemo, useRef, useEffect } from "react";
import { gsap } from "gsap";
import { MotionPathPlugin } from "gsap/MotionPathPlugin";
import styles from "./Section4.module.css";

// GSAP  
gsap.registerPlugin(MotionPathPlugin);

const Section4 = () => {
  const svgRef = useRef(null);

  //   
  const nodes = useMemo(() => [
    { id: 1, label: "", x: -550, y: 350, width: 200, height: 200, color: "#00a06a", fontSize: 30 },
    { id: 2, label: "Firebase", x: 0, y: 0, width: 250, height: 250, color: "#e74800", fontSize: 45 },
    { id: 3, label: "POS", x: -550, y: 0, width: 200, height: 200, color: "#4f75ff", fontSize: 30 },
    { id: 4, label: "NFC", x: -550, y: -350, width: 200, height: 200, color: "#00a06a", fontSize: 30 },
    { id: 5, label: "KIOSK", x: 550, y: 0, width: 200, height: 200, color: "#4f75ff", fontSize: 30 },
    { id: 6, label: "", x: 550, y: -350, width: 200, height: 200, color: "#00a06a", fontSize: 30 },
  ], []);

  //    (      )
  const edges = useMemo(() => [
    { id: 1, from: 1, to: 3, label: "pos  " }, // 
    { id: 2, from: 2, to: 3, label: "  " }, // Firebase  POS
    { id: 8, from: 3, to: 2, label: "  " }, // POS  Firebase
    { id: 3, from: 4, to: 6, label: "URL   " },
    { id: 4, from: 6, to: 5, label: "" },
    { id: 6, from: 3, to: 4, label: " URL " },
    { id: 7, from: 5, to: 2, label: "Firebase  " }, // KIOSK  Firebase
  ], []);

  // SVG   
  const defs = useMemo(() => (
    <defs>
      <marker
        id="arrowhead"
        markerWidth="10"
        markerHeight="7"
        refX="10"
        refY="3.5"
        orient="auto"
      >
        <polygon points="0 0, 10 3.5, 0 7" fill="#808080" />
      </marker>
    </defs>
  ), []);

  //  ID   
  const getNodeById = (id) => nodes.find((node) => node.id === id);

  //      
  const calculateOffset = (from, to, offset) => {
    const dx = to.x - from.x;
    const dy = to.y - from.y;
    const length = Math.sqrt(dx * dx + dy * dy);
    if (length === 0) return { x: 0, y: 0 };
    const ux = -dy / length;
    const uy = dx / length;
    return { x: ux * offset, y: uy * offset };
  };

  //   
  const labelPositions = useMemo(() => ({
    1: ['left'], // 'pos  ' - left
    2: ['bottom'], // '  ' - bottom
    8: ['top'], // '  ' - top
    3: ['top'], // 'URL   ' - top
    4: ['right'], // '' - right
    6: ['left'], // ' URL ' - left
    7: ['top'], // 'Firebase  ' - top
  }), []);

  //    ( )
  const renderLabel = (edge, line, index) => {
    const positionClass = labelPositions[edge.id] ? labelPositions[edge.id][index] || 'current' : 'current';

    //    ()
    let x = (getNodeById(edge.from).x + getNodeById(edge.to).x) / 2;
    let y = (getNodeById(edge.from).y + getNodeById(edge.to).y) / 2;

    //     
    switch (positionClass) {
      case 'left':
        x -= 40; //  
        break;
      case 'right':
        x += 40; //  
        break;
      case 'top':
        y -= 40; //  
        break;
      case 'bottom':
        y += 60; //  
        break;
      case 'current':
      default:
        break; //  
    }

    //   
    let textAnchor = 'middle';
    if (positionClass === 'left') textAnchor = 'end';
    if (positionClass === 'right') textAnchor = 'start';

    //   
    const labelStyle = {
      fontSize: '1.7rem',
      fontWeight: 'bold',
      fill: '#c4c4c4',
      pointerEvents: 'none',
      dominantBaseline: 'middle',
      textAnchor: textAnchor,
    };

    return (
      <text
        key={index}
        style={labelStyle}
        x={x}
        y={y}
      >
        {line}
      </text>
    );
  };

  //   
  const renderEdges = () => {
    //     
    const pastelPink = "#FFB6C1"; //   
    const pastelBlue = "#89CFF0"; //   

    return edges.map((edge) => {
      const fromNode = getNodeById(edge.from);
      const toNode = getNodeById(edge.to);

      if (!fromNode || !toNode) return null;

      const edgeId = `edge-${edge.id}`;
      const isBlueEdge = [2, 7].includes(edge.id); // Edge 2 (FirebasePOS), Edge 7 (KIOSKFirebase) are blue
      const arrowColor = isBlueEdge ? pastelBlue : pastelPink; //    

      let pathD = `M ${fromNode.x},${fromNode.y} L ${toNode.x},${toNode.y}`;
      if (edge.id === 8) {
        // Offset Edge 8 to prevent overlap with Edge 2
        const { x: ox, y: oy } = calculateOffset(fromNode, toNode, 20); // 20px 
        pathD = `M ${fromNode.x + ox},${fromNode.y + oy} L ${toNode.x + ox},${toNode.y + oy}`;
      }

      // Determine the number of arrows for this edge
      const numArrows = edge.id === 3 ? 8 : 5; // Edge id 3 (NFC  ) has 8 arrows

      return (
        <g key={edge.id}>
          {/* Path */}
          <path
            id={edgeId}
            d={pathD}
            stroke="#808080"
            strokeWidth="10"
            fill="none"
            markerEnd="url(#arrowhead)"
          />

          {/* Moving Arrows */}
          {[...Array(numArrows)].map((_, i) => (
            <polygon
              key={`forward-${edge.id}-arrow-${i}`}
              className="moving-arrow"
              data-path={edgeId}
              points="0 0, 20 10, 0 20"
              fill={arrowColor}
              pointerEvents="none"
            />
          ))}

          {/*   */}
          {edge.label.split("\n").map((line, index) => renderLabel(edge, line, index))}
        </g>
      );
    });
  };

  // GSAP 
  useEffect(() => {
    const speed = 400; //   (/)

    edges.forEach(edge => {
      const edgeId = `edge-${edge.id}`;
      const path = document.getElementById(edgeId);

      if (path) {
        const arrows = svgRef.current.querySelectorAll(`.moving-arrow[data-path="${edgeId}"]`);
        const pathLength = path.getTotalLength();
        const numArrows = edge.id === 3 ? 8 : 5; // Edge id 3 (NFC  ) has 8 arrows
        const duration = pathLength / speed;

        arrows.forEach((arrow, index) => {
          const start = index / numArrows;
          const end = 1 + (index / numArrows);

          gsap.to(arrow, {
            duration,
            repeat: -1,
            ease: "linear",
            motionPath: {
              path: path,
              align: path,
              alignOrigin: [0.5, 0.5],
              autoRotate: true,
              start,
              end,
            },
          });
        });
      }
    });
  }, [edges]);

  return (
    <div className={styles.section4}>
      <div className={styles.diagramContainer}>
        <svg
          ref={svgRef}
          viewBox="-600 -600 1200 1200"
          preserveAspectRatio="xMidYMid meet"
          xmlns="http://www.w3.org/2000/svg"
          width="100%"
          height="100%"
        >
          {defs}
          {renderEdges()}
          {nodes.map((node) => (
            <g key={node.id} transform={`translate(${node.x}, ${node.y})`}>
              <rect
                x={-node.width / 2}
                y={-node.height / 2}
                width={node.width}
                height={node.height}
                rx={20}
                ry={20}
                fill={node.color}
                stroke="none"
              />
              <text
                x="0"
                y="0"
                textAnchor="middle"
                fontSize={node.fontSize}
                fill="#fff"
                fontWeight="900"
                pointerEvents="none"
              >
                {node.label}
              </text>
            </g>
          ))}
        </svg>
      </div>
    </div>
  );
};

export default Section4;
