import React, { useRef, useEffect } from 'react';
import styles from './Section8.module.css';
import kioskIssueImage from '../../assets/images/ .jpg'; //   
import firebaseLogo from '../../assets/logo/firebase.svg'; // Firebase 
import openAILogo from '../../assets/logo/openai.svg'; // OpenAI 
import { gsap } from 'gsap'; // GSAP 

const Section8 = () => {
  const viewportImageRef = useRef(null);
  const firebaseLogoRef = useRef(null);
  const openAILogoRef = useRef(null);

  useEffect(() => {
    gsap.to(viewportImageRef.current, {
      scale: 1.1,
      duration: 1.5,
      ease: 'power1.inOut',
      repeat: -1,
      yoyo: true,
    });

    gsap.to(firebaseLogoRef.current, {
      rotation: 30,
      duration: 2,
      ease: 'power1.inOut',
      repeat: -1,
      yoyo: true,
    });

    gsap.to(openAILogoRef.current, {
      x: 10,
      duration: 0.5,
      ease: 'power1.inOut',
      repeat: -1,
      yoyo: true,
    });
  }, []);

  return (
    <div className={styles.developmentSection}>
      {/*   */}
      <h1 className={styles.sectionTitle}>    </h1>
      
      {/* " (css)"  */}
      <div className={styles.developmentContainer}>
        <h2> (css)</h2>
        <div className={styles.contentWrapper}>
          <img
            src={kioskIssueImage}
            alt=" "
            className={styles.problemImage}
            ref={viewportImageRef}
          />
          <p>
                  . <br />
              ,      <br />
            rootmargin    .<br />
               <span className={styles.emphasis}> css,  </span> .
          </p>
        </div>
      </div>

      {/* "Firebase "  */}
      <div className={styles.developmentContainer}>
        <h2>Firebase </h2>
        <div className={styles.contentWrapper}>
          <img
            src={firebaseLogo}
            alt="Firebase "
            className={styles.problemfirebaseImage}
            ref={firebaseLogoRef}
          />
          <p>
            Firebase       . <br />
                    . <br />
                  <br />
            <span className={styles.emphasis}>   </span>   .
          </p>
        </div>
      </div>

      {/* "GPT "  */}
      <div className={styles.developmentContainer}>
        <h2>GPT </h2>
        <div className={styles.contentWrapper}>
          <img
            src={openAILogo}
            alt="OpenAI "
            className={styles.problemImage}
            ref={openAILogoRef}
          />
          <p>
            LLM       . <br />
            <span className={styles.emphasis}>, ,   </span>  . <br />
              ,  <span className={styles.emphasis}>  </span>   .
          </p>
        </div>
      </div>
    </div>
  );
};

export default Section8;
