import React, { useEffect, useRef } from 'react';
import YouTube from 'react-youtube';
import styles from './Section5Video.module.css';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const Section5Video = () => {
  const sectionRef = useRef(null);
  const headingRef = useRef(null);
  const playerRef = useRef(null); // YouTube Player 

  // YouTube  
  const opts = {
    height: '100%',
    width: '100%',
    playerVars: {
      autoplay: 0, //   
      mute: 1,
      controls: 1,
      rel: 0,
      modestbranding: 1,
    },
  };

  const videoId = 'stwp4vslnmc';

  useEffect(() => {
    if (!headingRef.current) {
      console.error('   .');
      return;
    }

    // GSAP  
    const burnAndShineTimeline = gsap.timeline({ paused: true });

    //   textShadow  
    burnAndShineTimeline
      .to(headingRef.current, {
        textShadow: `
          0 0 20px rgba(255, 165, 0, 0.8),
          0 0 40px rgba(255, 140, 0, 0.7),
          0 0 60px rgba(255, 215, 0, 0.5)
        `,
        duration: 1.5,
        ease: 'power2.out',
      }, 0)
      .to(headingRef.current, {
        textShadow: `
          0 0 25px rgba(255, 165, 0, 0.9),
          0 0 50px rgba(255, 140, 0, 0.8),
          0 0 75px rgba(255, 215, 0, 0.6)
        `,
        duration: 1.5,
        ease: 'power2.in',
      }, '+=0')
      .to(headingRef.current, {
        textShadow: `
          0 0 20px rgba(255, 165, 0, 0.8),
          0 0 40px rgba(255, 140, 0, 0.7),
          0 0 60px rgba(255, 215, 0, 0.5)
        `,
        duration: 1.5,
        ease: 'power2.out',
      }, '+=0')
      .yoyo(true)
      .repeat(-1);

    ScrollTrigger.create({
      trigger: headingRef.current,
      start: 'top 80%',
      onEnter: () => {
        burnAndShineTimeline.play();
      },
      once: true,
      markers: false,
    });

    // IntersectionObserver   
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting && playerRef.current) {
            playerRef.current.seekTo(0); //    
            playerRef.current.playVideo(); //  
          } else if (playerRef.current) {
            playerRef.current.pauseVideo(); //  
          }
        });
      },
      { threshold: 0.5 } //  50%    
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => {
      observer.disconnect();
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
      burnAndShineTimeline.kill();
    };
  }, []);

  const onPlayerReady = (event) => {
    playerRef.current = event.target; // YouTube Player  
  };

  return (
    <div className={styles.section5VideoWrapper} ref={sectionRef}>
      <div className={styles.videoSection}>
        <YouTube 
          videoId={videoId} 
          opts={opts} 
          onReady={onPlayerReady} // Player Ready  
          className={styles.video}
        />
      </div>

      <div className={styles.headingContainer}>
        <h2 ref={headingRef} className={styles.heading}>
             
        </h2>
      </div>
      
      <div className={styles.textGrid}>
        <div className={styles.textSection1}>
          <p>
                   .
            ,            
                 . <br />
                   , <span className={styles.highlight}>      
            </span>   .
                   UI   .
          </p>
        </div>

        <div className={styles.textSection2}>
          <p>
                <span className={styles.highlight}> </span> .<br />
             <span className={styles.highlight}>  </span> . 
                   .<br />
             <span className={styles.highlight}> UI  </span>. 
               
                  .<br />
             <span className={styles.highlight}> </span>  . 
                 , 
                  .
          </p>
        </div>
      </div>
    </div>
  );
};

export default Section5Video;
