// src/components/Section5/Section5.jsx
import React, { useEffect, useRef, useState } from 'react';
import styles from './Section5.module.css';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import Tooltip from './Tooltip/Tooltip';

import posLogin from '../../assets/images/pos.png';
import posSignup from '../../assets/images/pos.png';
import posMain from '../../assets/images/pos.png';
import posQuickButton from '../../assets/images/pos.png';
import posSettings from '../../assets/images/pos.png';
import posTableEdit from '../../assets/images/pos.png';
import posMenuEdit1 from '../../assets/images/pos.png';

const Section5 = () => {
  const sectionRef = useRef(null);
  const imageContainerRef = useRef(null);

  const [tooltip, setTooltip] = useState({
    visible: false,
    x: 0,
    y: 0,
    title: '',
    text: '',
  });

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);

    const section = sectionRef.current;
    const imageContainer = imageContainerRef.current;

    gsap.to(imageContainer, {
      x: () => -(imageContainer.scrollWidth - window.innerWidth),
      ease: 'none',
      scrollTrigger: {
        trigger: section,
        start: 'top top',
        end: () => `+=800vh`,
        scrub: 1,
        pin: true,
        anticipatePin: 1,
        markers: false,
      },
    });

    const handleResize = () => {
      ScrollTrigger.refresh();
    };

    window.addEventListener('resize', handleResize);

    return () => {
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill());
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const handleMouseEnter = (e, title, text) => {
    setTooltip({
      visible: true,
      x: e.clientX,
      y: e.clientY,
      title,
      text,
    });
  };

  const handleMouseMove = (e) => {
    if (tooltip.visible) {
      setTooltip((prev) => ({
        ...prev,
        x: e.clientX,
        y: e.clientY,
      }));
    }
  };

  const handleMouseLeave = () => {
    setTooltip({
      visible: false,
      x: 0,
      y: 0,
      title: '',
      text: '',
    });
  };

  return (
    <div className={styles.posSection5} ref={sectionRef}>
      <Tooltip
        visible={tooltip.visible}
        x={tooltip.x}
        y={tooltip.y}
        title={tooltip.title}
        text={tooltip.text}
      />

      <div className={styles.posHorizontalScroll}>
        <div
          className={styles.posImageContainer}
          ref={imageContainerRef}
          onMouseMove={handleMouseMove}
        >
          {[
            {
              img: posLogin,
              alt: 'POS ',
              tooltipTitle: '',
              tooltipText: '    .',
            },
            {
              img: posSignup,
              alt: 'POS ',
              tooltipTitle: '',
              tooltipText: '     .',
            },
            {
              img: posMain,
              alt: 'POS ',
              tooltipTitle: ' ',
              tooltipText: 'POS    .',
            },
            {
              img: posQuickButton,
              alt: 'POS ',
              tooltipTitle: '  ',
              tooltipText: '   .',
            },
            {
              img: posSettings,
              alt: 'POS ',
              tooltipTitle: '',
              tooltipText: 'POS     .',
            },
            {
              img: posTableEdit,
              alt: 'POS ',
              tooltipTitle: ' ',
              tooltipText: [
                '   .',
                ' , , ,    .',
              ],
            },
            {
              img: posMenuEdit1,
              alt: 'POS ',
              tooltipTitle: ' ',
              tooltipText: '     .',
            },
          ].map((item, index) => (
            <div
              key={index}
              className={styles.posImageBox}
              onMouseEnter={(e) =>
                handleMouseEnter(e, item.tooltipTitle, item.tooltipText)
              }
              onMouseLeave={handleMouseLeave}
            >
              <img src={item.img} alt={item.alt} loading="lazy" />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Section5;
