import React, { useEffect, useRef, useState } from 'react';
import styles from './Section4Video.module.css';
import SystemVideo from '../../assets/video/ .mp4';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

// GSAP ScrollTrigger  
gsap.registerPlugin(ScrollTrigger);

const Section4Video = () => {
  const sectionRef = useRef(null); //    
  const videoRef = useRef(null); //    
  const headingRef = useRef(null); //    
  const [isVideoLoaded, setIsVideoLoaded] = useState(false); //    

  useEffect(() => {
    const currentVideo = videoRef.current; //    

    if (!currentVideo) {
      return;
    }

    // IntersectionObserver :  25%    
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setIsVideoLoaded(true); //    
            observer.unobserve(entry.target); //    
          }
        });
      },
      {
        threshold: 0.25, //  25%    
      }
    );

    //    
    observer.observe(currentVideo);

    //  :     
    return () => {
      if (currentVideo) {
        observer.unobserve(currentVideo);
      }
    };
  }, []);

  useEffect(() => {
    if (!headingRef.current) {
      console.error('   .');
      return;
    }

    // GSAP  :     
    const burnAndShineTimeline = gsap.timeline({ paused: true });

    //   textShadow  
    burnAndShineTimeline
      .to(headingRef.current, {
        textShadow: `
          0 0 20px rgba(255, 165, 0, 0.8),
          0 0 40px rgba(255, 140, 0, 0.7),
          0 0 60px rgba(255, 215, 0, 0.5)
        `, //   textShadow  ( )
        duration: 1.5,
        ease: 'power2.out',
      }, 0) //  

      .to(headingRef.current, {
        textShadow: `
          0 0 25px rgba(255, 165, 0, 0.9),
          0 0 50px rgba(255, 140, 0, 0.8),
          0 0 75px rgba(255, 215, 0, 0.6)
        `, //   textShadow  (   )
        duration: 1.5,
        ease: 'power2.in',
      }, '+=0') //   

      .to(headingRef.current, {
        textShadow: `
          0 0 20px rgba(255, 165, 0, 0.8),
          0 0 40px rgba(255, 140, 0, 0.7),
          0 0 60px rgba(255, 215, 0, 0.5)
        `, //   textShadow  (  )
        duration: 1.5,
        ease: 'power2.out',
      }, '+=0') //   

      .yoyo(true) //   
      .repeat(-1); //   

    // ScrollTrigger :      
    ScrollTrigger.create({
      trigger: headingRef.current, //  : 
      start: 'top 80%', //   
      onEnter: () => {
        burnAndShineTimeline.play(); //  
      },
      once: true, //    
      markers: false, //   
    });

    //  :    ScrollTrigger   
    return () => {
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
      burnAndShineTimeline.kill();
    };
  }, []);

  return (
    <div className={styles.section4VideoWrapper} ref={sectionRef}>
      {/*   */}
      <div className={styles.videoSection} ref={videoRef}>
        {isVideoLoaded && (
          <video
            src={SystemVideo}
            className={styles.video}
            autoPlay
            loop
            muted
            playsInline
            preload="auto"
          />
        )}
      </div>

      {/*   */}
      <div className={styles.headingContainer}>
        {/* ' UI  '  */}
        <h2 ref={headingRef} className={styles.heading}>
           UI  
        </h2>
      </div>
      
      {/*    */}
      <div className={styles.textGrid}>
        <div className={styles.textSection1}>
          <p>
              UI    .
           REACT  ,    UI    . 
            <br /><br />
            React  UI <span className={styles.highlight}>    </span>   
                  . 
            , <span className={styles.highlight}>   </span>, 
                     . 
                <span className={styles.highlight}>   UI</span> 
                .
          </p>
        </div>

        <div className={styles.textSection2}>
          <p>
            Firebase       , 
            UI      . 
            UI   ,     UI   . <br /><br />
                  , <span className={styles.highlight}>      </span> 
            <span className={styles.highlight}>  </span>    .    (UX)   
             UI  <span className={styles.highlight}> </span>    .
          </p>
        </div>
      </div>
    </div>
  );
};

export default Section4Video;
